<template>
  <div id="settlement">
    <!-- 订单结算头部 -->
    <div class="settlementHead">
      <div class="settlementLogo">
        <img src="../assets/images/logo.jpeg" alt="logo">
      </div>
      <div class="settlementTotal">填写并核对订单</div>
    </div>
    <!-- 订单结算内容 -->
    <div class="settlementMain">
      <!-- 地址 -->
      <div class="aabs">
        <aabs @ReturnID='getAddressID'></aabs>
      </div>
      <!-- 支付方式 -->
      <div class="payment">
        <div class="payment_p"><span>支付方式</span></div>
        <div class="payment_mode">
          <div class="payment_g">货到付款</div>
          <div class="payment_on active">在线支付</div>
        </div>
      </div>
      <!-- 送货清单 -->
      <div class="deliveryList">
        <div class="DL_title">
          <span>送货清单</span>
        </div>
        <div class="DL_items" v-for="(item, index) in ProductList" :key="index">
          <div class="Distribution">
            <div class="DL_dTop">配送方式</div>
            <div class="DL_dBottom">快递配送</div>
          </div>
          <div class="DL_iImg">
            <img :src="item.image" alt="">
          </div>
          <div class="DL_iDetailed">
            <div>{{item.sell_point}}</div>
          </div>
          <div class="DL_iPrice">
            <span>￥&nbsp;</span><span>{{item.price}}</span>
          </div>
          <div class="DL_iQuantity">
            <span>x&nbsp;</span><span>{{item.count}}</span>
          </div>
        </div>
      </div>
      <!-- 发票信息 -->
      <div class="invoice">
        <div class="Invoice_top">发票信息</div>
        <div class="Invoice_bon">
          <div>普通发票</div>
          <div>个人</div>
          <div>商品明细</div>
        </div>
      </div>
    </div>
    <!-- 订单结算尾部 -->
    <div class="settlementTail">
      <div class="ST_top">
        <span>{{this.buy.length}} 种商品，总金额: </span><span>￥ </span><span>{{sumprice}}</span>
      </div>
      <div class="ST_bon">
        <div><span style="margin-right: 25px;">应支付金额： </span><span style="color: red">￥ </span><span style="color: red;font-size: 20px;">{{sumprice}}</span></div>
      </div>
      <div class="ST_bBtn" @click="ONModel">
        提交订单
      </div>
    </div>
    <!-- 模态框 -->
    <div class="settlementModal" :class="{ModalActive : isModalActive}">
      <div class="ModalCenten">
        <div class="icon iconfont icon-guanbi" @click="OFFModel"></div>
        <div class="ModalImg"><img src="../assets/images/dianzan.jpeg" alt=""></div>
        <div class="ModalBtn" @click="payment">完成支付</div>
      </div>
    </div>
  </div>
</template>

<script>
import aabs from '../components/S.address.vue'
import { mapState, mapGetters } from 'vuex'
export default {
  components: {
    aabs
  },
  data() {
    return {
      // 商品列表
      ProductList: [],
      // 价格
      sumprice: 0,
      // 收获地址ID
      addressID: '14',
      // 留言
      note: '',
      // 模态框样式
      isModalActive: false
    }
  },
  computed: {
    ...mapState('shopcart', ['buy']),
    ...mapGetters('shopcart', ['getBuy'])
  },
  created () {
    // 获取商品列表
    this.getProductList()
  },
  methods: {
    // 获取商品列表
    getProductList () {
      const idArr = []
      this.buy.forEach(item => {
        idArr.push(item.id)
      })
      this.$http.get('shopcart', {
        params: {
          ids: idArr
        }
      }).then(res => {
        // console.log(res)
        if (res.data.code === 2) {
          alert(res.data.msg)
          this.$router.push('/denglu')
        } else if (res.data.code === 0) {
          alert(res.data.msg)
        } else {
          this.ProductList = res.data.data
          // 计算金额
          var getBuyList = this.getBuy
          let sumprice = 0
          this.ProductList.forEach(item => {
            if (item.id === getBuyList[item.id].id) {
              item.count = getBuyList[item.id].count
              sumprice += item.price * getBuyList[item.id].count
            }
          })
          this.sumprice = sumprice
        }
      })
    },
    // 获取收货地址id
    getAddressID (val) {
      this.addressID = val
    },
    ONModel () {
      this.isModalActive = true
      this.note = this.sumprice
    },
    OFFModel () {
      this.isModalActive = false
    },
    // 完成支付
    payment () {
      this.$http.post('order/create', {
        address: this.addressID,
        note: this.note,
        godds: this.buy
      }).then(res => {
        if (res.data.code === 2) {
          alert(res.data.msg)
          this.$router.push('/denglu')
        } else if (res.data.code === 0) {
          alert(res.data.msg)
        } else {
          this.$store.commit('shopcart/removeCar')
          alert(res.data.msg)
          this.$router.push('/my')
        }
        this.OFFModel()
      })
    }

  }
}
</script>

<style src='../assets/shoppingcss/settlement.css' scoped></style>
<style scoped>
#settlement{
  padding-top: 80px;
}
</style>
