<template>
    <div class="backTop" :class="{ is_fixed: isFixed }" @click="backTop">
      <span class="iconfont icon-huidaodingbu"></span>
    </div>
</template>

<script>
export default {
  name: 'backTop',
  data() {
    return {
      isFixed: false
    }
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll)
  },
  methods: {
    // 样式显示
    handleScroll () {
      var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop // 滚动条偏移量
      var Top = document.querySelector('.backTop')
      var offsetTop = Top.offsetTop
      if (scrollTop > offsetTop) {
        this.isFixed = true
      } else {
        this.isFixed = false
      }
    },
    // 返回顶部方法
    animate(obj, target, callback) {
      clearInterval(obj.timer)
      obj.timer = setInterval(function () {
        // 步长值写到定时器里面 每次执行定时器就会重新技术按这个值
        // (目标值 - 现在位置) / 10做为每次移动的距离
        var step = (target - window.pageXOffset) / 10
        step = step > 0 ? Math.ceil(step) : Math.floor(step)
        if (window.pageXOffset === target) {
          clearInterval(obj.timer)
          if (callback) {
            // 使用调用函数
            callback()
          }
        }
        window.scroll(0, window.pageXOffset + step)
      }, 30)
    },
    backTop () {
      this.animate(window, 0)
    }
  }
}
</script>

<style scoped>
.backTop {
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  border-radius: 10px;
  background-color: #fff;
  opacity: 0;
  cursor: pointer;
  transition: all 1s ease;
}
.is_fixed {
  opacity: 1;
  transform: translateY(100px);
}
</style>
