<template>
  <div id="home">
    <!-- 轮播图 -->
    <div class="banner">
      <el-carousel indicator-position="outside" :interval="5000">
        <el-carousel-item v-for="(item, index) in banner" :key="index">
          <div class="img">
            <img :src="item.img" alt="">
          </div>
        </el-carousel-item>
      </el-carousel>
    </div>
    <!-- 商品内容 -->
    <div class="home_content">
      <div class="home_main">
        <div class="home_top">
          <div class="home_top_items">
            <span class="span" @click='id = 1' :class="{ avtive: id === 1 }">手机数码</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 10' :class="{ avtive: id === 10 }">家用电器</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 19' :class="{ avtive: id === 19 }">电脑办公</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 29' :class="{ avtive: id === 29 }">汽车用品</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 39' :class="{ avtive: id === 39 }">男装</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 49' :class="{ avtive: id === 49 }">女装</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 59' :class="{ avtive: id === 59 }">钟表宝珠</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 66' :class="{ avtive: id === 66 }">内衣饰品</span>
          </div>
          <div class="home_top_items">
            <span class="span" @click='id = 73' :class="{ avtive: id === 73 }">箱包手袋</span>
          </div>
        </div>
        <div class="home_bottom">
          <div class="home_bottom_items" v-for="(item, index) in classMain" :key="index">
            <router-link :to="'/ProductList/' + item.id">
              <div class="home_bottom_img">
                <img :src="item.image" alt="">
              </div>
            </router-link>
          </div>
        </div>
      </div>
    </div>
    <div class="taiyang01">
    </div>
    <statement></statement>
  </div>
</template>

<script>
// import $ from 'jquery'
import statement from '../views/A.statement.vue'
export default {
  name: 'home',
  components: {
    statement
  },
  data() {
    return {
      banner: [],
      classList: [],
      classMain: [],
      id: '1'
    }
  },
  watch: {
    id () {
      this.Ids()
    }
  },
  created () {
    this.lunbo()
    this.classify()
    this.id = 1
  },
  methods: {
    // 轮播
    lunbo () {
      this.$http
        .get('imglist', {
          params: {}
        })
        .then(res => {
          // console.log(res)
          if (res.data.code === 1) {
            this.banner = res.data.data
          }
        })
    },
    // 分类信息
    classify () {
      this.$http
        .get('category', {
          params: {}
        })
        .then(res => {
          if (res.data.code === 1) {
            // console.log(res)
            this.classList = res.data.data
            this.classMain = res.data.data[0].sub
            // console.log(res.data.data[0].sub)
          }
        })
    },
    Ids () {
      if (this.id === 1) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[0].sub
            }
          })
      }
      if (this.id === 10) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[1].sub
            }
          })
      }
      if (this.id === 19) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[2].sub
            }
          })
      }
      if (this.id === 29) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[3].sub
            }
          })
      }
      if (this.id === 39) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[4].sub
            }
          })
      }
      if (this.id === 49) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[5].sub
            }
          })
      }
      if (this.id === 59) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[6].sub
            }
          })
      }
      if (this.id === 66) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[7].sub
            }
          })
      }
      if (this.id === 73) {
        this.$http
          .get('category', {
            params: {}
          })
          .then(res => {
            if (res.data.code === 1) {
              this.classMain = res.data.data[8].sub
            }
          })
      }
    }
  }
}
</script>

<style src='../assets/homecss/home.css' scoped></style>
<style scoped>
#home{
 padding-top: 80px;
}
#home .home_content{
  min-height: 100vh;
}
#home .avtive{
  color: rgb(255, 254, 254)!important;
}
#home >>> .el-carousel__indicator--horizontal{
  padding: 12px 12px;
}
#home >>> .el-carousel__button{
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: red;
}
</style>
