<template>
  <div id='denglu'>
        <!-- 整体布局 -->
        <div class="container" v-bind:class="{active:isYD}">
            <!-- 注册框 -->
            <div class="container_from  container--signup">
                <form action="#" class="from" id="from1">
                    <h2 class="from_title">注册账号</h2>
                    <input type="text" placeholder="User" v-model="logoutFrom.username" class="input">
                    <input type="email" placeholder="Email" v-model="logoutFrom.email" class="input">
                    <input type="password" placeholder="Password" v-model="logoutFrom.password" class="input">
                    <button class="btn" @click.prevent='logout()'>点击注册</button>
                </form>
            </div>
            <!-- 登录框 -->
            <div class="container_from container--signin">
                <form action="#" class="from" id="from2">
                    <h2 class="from_title">登录账号</h2>
                    <input v-model="loginFrom.username" type="text" placeholder="Username" class="input">
                    <input v-model="loginFrom.password" type="password" placeholder="Password" class="input">
                    <a href="#" class="link">忘记密码？</a>
                    <button class="btn" @click.prevent='login()'>登录</button>
                </form>
            </div>

            <div class="container_overlay">
                <div class="overlay">
                    <div class="overlay_panel overlay--left">
                        <button class="btn" id="signin" @click='quehuan'>已有账号，点击登录</button>
                    </div>

                    <div class="overlay_panel overlay--right">
                        <button class="btn" id="signup" @click='quehuan'>没有账号,点击注册</button>
                    </div>
                </div>
            </div>
        </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 切换样式
      isYD: false,
      // 登录内容
      loginFrom: {
        username: '臭脚妹妹',
        password: '123456'
      },
      logoutFrom: {
        username: '',
        password: '123456',
        email: ''
      }
    }
  },
  methods: {
    // 却换样式
    quehuan () {
      this.isYD = !this.isYD
    },
    // 登录操作
    // 1.判断用户名和密码是否输入
    login () {
      console.log(this.loginFrom)
      if (this.loginFrom.username === '' || this.loginFrom.password === '') {
        alert('请输入正确的账号与密码')
      } else {
        this.$http.post('/login', this.loginFrom).then(res => {
          if (res.data.code === 1) {
            alert(res.data.msg)
            // 登陆成功后跳转页面
            this.$router.push('/home')
            // 登陆成功后，需要将用户的信息保存到vueX中
            this.$store.commit('user/setUser', res.data.data)
            // 将用户登陆的session_id 保存到localStorage里面
            this.$auth.setAuthorization(res.data.data.session_id)
          } else {
            alert('账号或者密码错误')
          }
        })
      }
    },
    logout () {
      if (this.logoutFrom.username === '' || this.logoutFrom.password === '' || this.logoutFrom.email === '') {
        alert('请输入正确的账号与密码')
      } else {
        this.$http.post('/register', this.logoutFrom).then(res => {
          alert(res.data.msg)
          console.log(res)
          this.logoutFrom = {
            username: '',
            password: '',
            email: ''
          }
        })
      }
    }
  }
}
</script>

  <style>
/* :root根伪类 --为自定义css属性 方便我们重复使用同一个属性 自定义属性使用var()进行调用 */
:root {
  /* 背景颜色 */
  --white: #e9e9e9;
  --gray: #333;
  --blue: #0367a6;
  --lightblue: #0c9faf;
  /* 外边框圆角属性 */
  --button-radius: 0.7rem;

  --max-width: 750px;
  --max-height: 420px;
  /* 字体样式 */
  font-size: 16px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}

#denglu{
  align-items: center;
  background-color: var(--white);
  background: url(../assets/images/002.png);
  background-attachment: fixed;
  /*随着页面的滚动轴背景图片不会移动 */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  /* 把背景图片放大到适合元素容器的尺寸，图片比例不变，但是要注意，超出容器的部分可能会裁掉 */
  height: 100vh;
  display: grid;
  /*网格布局*/
  place-items: center;
  /* 这里为什么使用网格布局呢？
    因为盒子如果使用定位来让他绝对居中，
    那么其子盒子也会相应父盒子的定位，
    导致子盒子的背景图片无法与body的背景图片完全一样 */
}

#denglu .from_title {
  font-weight: 300;
  margin: 0;
  margin-bottom: 1.25rem;
}

#denglu .link {
  color: var(--gray);
  font-size: 0.9rem;
  margin: 1.5rem 0;
  text-decoration: none;
}

#denglu .container {
  background-color: var(--white);
  border-radius: var(--button-radius);
  box-shadow: 0 0.9rem 1.7rem rgba(0, 0, 0, 0.25);
  height: var(--max-height);
  max-width: var(--max-width);
  overflow: hidden;
  position: relative;
  width: 100%;
}

#denglu .container_from {
  height: 100%;
  position: absolute;
  top: 0;
  transition: all 0.6s ease-in-out;
}

#denglu .container--signin {
  left: 0;
  width: 50%;
  z-index: 2;
}

#denglu .container.active .container--signin {
  transform: translateX(100%);
}

#denglu .container--signup {
  left: 0;
  opacity: 0;
  width: 50%;
  z-index: 1;
}

#denglu .container.active .container--signup {
  animation: show 0.6s;
  opacity: 1;
  transform: translateX(100%);
  z-index: 5;
}

#denglu .container_overlay {
  height: 100%;
  left: 50%;
  overflow: hidden;
  position: absolute;
  top: 0;
  transition: transform 0.6s ease-in-out;
  width: 50%;
  z-index: 100;
}

#denglu .container.active .container_overlay {
  transform: translateX(-100%);
}

#denglu .overlay {
  background-color: var(--lightblue);
  background: url(../assets/images/002.png);
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 100%;
  width: 200%;
  position: relative;
  left: -100%;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

#denglu .container.active .overlay {
  transform: translateX(50%);
}

#denglu .overlay_panel {
  align-items: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  position: absolute;
  text-align: center;
  top: 0;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
  width: 50%;
}

#denglu .overlay--left {
  transform: translateX(-20%);
}

#denglu .container.active .overlay--left {
  transform: translateX(0);
}

#denglu .overlay--right {
  right: 0;
  transform: translateX(0);
}

#denglu .container.active .overlay--right {
  transform: translateX(20%);
}

#denglu .btn {
  background-color: var(--blue);
  background-image: linear-gradient(
    90deg,
    var(--blue) 0%,
    var(--lightblue) 74%
  );
  border-radius: 20px;
  border: 1px solid var(--blue);
  color: var(--white);
  cursor: pointer;
  font-size: 0.8rem;
  font-weight: bold;
  letter-spacing: 0.1rem;
  padding: 0.9rem 4rem;
  text-transform: uppercase;
  transition: transform 80ms ease-in-out;
}

#denglu .form > .btn {
  margin-top: 1.5rem;
}

#denglu .btn:active {
  transform: scale(0.95);
}

#denglu .btn:focus {
  outline: none;
}

#denglu .from {
  background-color: var(--white);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  /*  垂直显示 */
  padding: 0 3rem;
  height: 100%;
  text-align: center;
}

#denglu .input {
  background-color: #fff;
  border: none;
  padding: 0.9rem 0.9rem;
  margin: 0.5rem 0;
  width: 100%;
}

@keyframes show {
  0%,
  49.99% {
    opacity: 0;
    z-index: 1;
  }

  50%,
  100% {
    opacity: 1;
    z-index: 5;
  }
}
</style>
