function setItem (name, item) {
  // 顺便存储到本地
  // 存
  window.localStorage.setItem(name, JSON.stringify(item))
}
// 取
function getItem (name) {
  return JSON.parse(localStorage.getItem(name) || '[]')
}
export default {
  state: {
    // car:[{id:商品id，count：数量，select:是否选中}，{}，{}]
    // 购物车
    car: getItem('car'),
    // 需要购买的
    buy: getItem('buy')
  },
  // 数据处理
  mutations: {
    // 添加商品到购物车
    addShopcart (state, spDetails) {
      // 如果购物车有了 spDetails 对应的商品，那么就让商品的数量加一
      // 否者把 spDetails 添加到购物车中
      let flag = false // 当 flag 为 false时，说明购物车里面没有相同的商品
      // 判断购物车中是否有于 spDetails 对应的商品 如果有就增加数量
      state.car.some(item => {
        if (parseInt(item.id) === parseInt(spDetails.id)) {
          item.count += parseInt(spDetails.count)
          flag = true
          return true
        }
      })
      if (!flag) {
        // 购物车里面没有相同的商品
        state.car.push(spDetails)
      }
      // 调用 setItem 将购物车的数据保存到 localStorage 里面
      setItem('car', state.car)
    },
    // 当点击了加减号获得的最新数量，保存进数据里面
    updataShopcart (state, spDetails) {
      state.car.forEach(item => {
        if (parseInt(item.id) === parseInt(spDetails.id)) {
          item.count = parseInt(spDetails.count)
        }
        return true
      })
      setItem('car', state.car)
    },
    // 修改商品的选中状态
    updataShopSelected (state, spDetails) {
      state.car.forEach(item => {
        if (parseInt(item.id) === parseInt(spDetails.id)) {
          item.selected = spDetails.selected
        }
      })
      setItem('car', state.car)
    },
    // 根据id删除商品
    removeShopcart (state, id) {
      state.car.forEach((item, index) => {
        if (parseInt(item.id) === parseInt(id)) {
          state.car.splice(index, 1)
          return true
        }
      })
      setItem('car', state.car)
    },
    // 将购物车中的商品添加到buy数组里面
    getShopbuy (state) {
      state.buy = []
      state.car.forEach(item => {
        if (item.selected) {
          state.buy.push(item)
        }
      })
      setItem('buy', state.buy)
    },
    // 清空购物车
    removeCar (state) {
      state.car = []
      setItem('car', state.car)
    }
  },
  actions: {},
  // 计算属性
  getters: {
    // 获取商品数量
    getCommodityCount (state) {
      var commdity = {}
      state.car.forEach(item => {
        commdity[item.id] = item.count
      })
      return commdity
    },
    // 获取商品有没有选中的状态值（selected）
    getCommoditySelected (state) {
      var commdity = {}
      state.car.forEach(item => {
        commdity[item.id] = item.selected
      })
      return commdity
    },
    // 获取选中的商品的件数
    getSelectedCount (state) {
      var count = 0
      state.car.forEach(item => {
        if (item.selected === true) {
          count += item.count
        }
      })
      return count
    },
    // 获取选中商品
    getSelectedCommdity (state) {
      var commdity = {}
      state.car.forEach(item => {
        if (item.selected) {
          commdity[item.id] = item
        }
      })
      return commdity
    },
    // 获取需要购买的商品
    getBuy (state) {
      var commdity = {}
      state.buy.forEach(item => {
        commdity[item.id] = item
      })
      return commdity
    }
  },
  namespaced: true
}
