import Vue from 'vue'
import VueRouter from 'vue-router'

import home from '../components/02.home.vue'
import shopping from '../components/03.shopping.vue'
import my from '../components/04.my.vue'
import video from '../components/05.video.vue'
import denglu from '../components/denglu.vue'

// home
import ProductList from '../views/H.ProductList.vue'
import productDetails from '../views/H.productDetails.vue'

// shopping
import settlement from '../views/S.settlement.vue'

// 查询页面
const result = () => import('../views/result.vue')

// 我的音乐
const myMusic = () => import('../components/06.myMusic.vue')
const personality = () => import('../views/IN.personality.vue')
const recommend = () => import('../views/IN.recommend.vue')
const newMusic = () => import('../views/IN.newMusic.vue')
const newMV = () => import('../views/IN.newMV.vue')
const songSheet = () => import('../views/IN.songSheet.vue')
const detailedMV = () => import('../views/IN.detailedMV.vue')
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    redirect: '/home'
  },
  {
    // 地址   发现音乐
    path: '/myMusic',
    name: 'myMusic',
    // 组件
    component: myMusic,
    children: [
      {
        path: 'personality',
        name: 'personality',
        components: {
          myIndex: personality
        }
      },
      {
        path: 'recommend',
        name: 'recommend',
        components: {
          myIndex: recommend
        }
      },
      {
        path: 'newMusic',
        name: 'newMusic',
        components: {
          myIndex: newMusic
        }
      },
      {
        path: 'newMV',
        name: 'newMV',
        components: {
          myIndex: newMV
        }
      },
      {
        path: 'songSheet',
        name: 'songSheet',
        components: {
          myIndex: songSheet
        }
      },
      {
        path: 'detailedMV',
        name: 'detailedMV',
        components: {
          myIndex: detailedMV
        }
      },
      {
        // 查询
        path: '/result',
        name: 'result',
        components: {
          myIndex: result
        }
      }
    ]
  },
  {
    // 地址   shopping
    path: '/shopping',
    // 组件
    component: shopping
  },
  {
    // 地址   settlement
    path: '/shopping/settlement',
    name: 'settlement',
    props: true,
    // 组件
    component: settlement
  },
  {
    // 地址   my
    path: '/my',
    // 组件
    component: my
  },
  {
    // 地址   video
    path: '/video',
    // 组件
    component: video
  },
  {
    // 地址   home
    path: '/home',
    // 组件
    component: home,
    props: true
  },
  {
    // ProductList
    path: '/ProductList/:category_id',
    component: ProductList,
    props: true
  },
  {
    // 地址   登录
    path: '/denglu',
    name: 'denglu',
    // 组件
    component: denglu
  },
  {
    // 商品详细
    path: '/productDetails/:id',
    naem: 'productDetails',
    props: true,
    component: productDetails
  }
]

const router = new VueRouter({
  // mode: 'history',
  routes
})

export default router
