<template>
  <div id="my">
    <div class="myorder">
      <!-- 订单 -->
      <div class="order">
        <div class="myMsg">
          <div class="myImg"><img src="../assets/images/sjmv.jpg" alt=""></div>
          <span>{{$store.state.user.username}}</span>
        </div>
        <div class="title">
          <div><span>待付款</span></div>
          <div><span>待发货</span></div>
          <div><span>待收货</span></div>
          <div><span>待评价</span></div>
          <div class="spanEnd"><span >退款</span></div>
        </div>
        <div class="items" v-for="(item, index) in setList" :key="index">
          <div v-if="(index + 1) < 10">0{{index+1}}</div>
          <div v-else>{{index+1}}</div>
          <div>买给：&nbsp;<span style="color: rgb(88, 8, 235)">{{item.address_name}}</span>&nbsp;的商品</div>
          <div class="items_site"><span>地址:</span><span>{{item.address_area}}</span><span>{{item.address_detail}}</span></div>
          <div>
            <span class="viewOrder" @click="boxShow(item.id)">查看订单</span>
            <span class="cancel" style="margin-left: 10px;cursor: pointer;" @click.once="cancelOrder(item.id, index)">取消</span>
          </div>
        </div>
        <div class="animation"><span class="icon vehicle iconfont icon-dahuoche"></span></div>
      </div>
      <!-- 时间日期 -->
      <div class="times">
        <div class="times_t">日&nbsp;历</div>
        <div class="times_b">
          <div class="number">{{this.dates}}</div>
          <div class="week">{{day}}</div>
          <div class="specificDate">{{this.year}}-{{this.month}}</div>
        </div>
      </div>
    </div>
    <!-- 订单详细模态框 -->
    <div class="orderDetails" :class="{orderDetailsShow: isorderDetailsShow}">
      <form action="#">
        <div class="icon iconfont icon-guanbi" @click="boxHide"></div>
        <div class="forms">
          <div class="forms_top">
            <span>地址：</span>
            <div class="address_area">广东</div>
            <div class="address_detail">广州</div>
          </div>
          <div class="forms_in">
            <span>电话：</span>
            <div class="address_tel">13688888888</div>
          </div>
          <div class="forms_bom">
          <span>买给</span><span class="address_name">{{this.showLists.address_name}}</span><span>的</span>
          <div class="goods_items" v-for="(item, index) in showLists" :key="index">
            <div class="goods_name">一加</div>
            <div class="goods_price">￥&nbsp;&nbsp;999</div>
            <div class="goods_count">x&nbsp;1</div>
          </div>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data () {
    return {
      // 订单列表
      setList: [],
      // 查询订单详情
      showLists: [],
      // 年
      year: '',
      // 月
      month: '',
      // 日
      dates: '',
      // 周几
      day: '',
      // 模态框
      isorderDetailsShow: false,
      cancel: true
    }
  },
  created () {
    // 查询订单列表
    this.setOrderList()
    // 获取日期
    this.newData()
    // console.log(this.showLists)
  },
  methods: {
    // 查询订单列表
    setOrderList () {
      this.$http
        .get('order/list')
        .then(res => {
          // console.log(res)
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            this.setList = res.data.data
          }
        })
    },
    // 获取日期
    newData () {
      var date = new Date()
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      // 月份 返回的月份小一个月 记得月份+1
      var dates = date.getDate()
      var day = date.getDay()
      var arr = ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六']
      this.year = year
      this.month = month
      this.dates = dates
      this.day = arr[day]
    },
    // 查询订单详情
    boxShow (Id) {
      console.log(Id)
      this.isorderDetailsShow = true
      this.$http
        .get('order/show', { id: Id })
        .then(res => {
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            console.log(res)
            this.showLists = ''
            this.showLists = res.data.data.user_order_goods
            console.log(this.showLists)
          }
        })
    },
    // 关闭模态框
    boxHide () {
      this.isorderDetailsShow = false
    },
    // 取消订单
    cancelOrder (ID, index) {
      this.$http
        .post('order/cancel', { id: ID })
        .then(res => {
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            alert(res.data.msg)
            // console.log($('.cancel')[index].innerHTML)
            $('.cancel')[index].innerHTML = '已取消'
          }
        })
    }
  }
}
</script>

<style src='../assets/shoppingcss/my.css' scoped></style>
<style scoped>
#my{
  width: 100%;
  padding-top: 80px;
}
</style>
