<template>
  <div id="fullpage">
    <div class="aaa"></div>
    <full-page :options="options">
      <div class="section section1">
        <div class="s1_left"><img src="../assets/fullpage/images/silver-phone-47094e.png.webp" alt=""></div>
        <div class="s1_right"><img src="../assets/fullpage/images/black-phone-58a273.png.webp" alt=""></div>
        <div class="s1_text">
          <span>一加9RT</span>
          <span>速度有了新名字</span>
          <span style="font-size: 30px;">￥ 3299 起</span>
        </div>
      </div>
      <div class="section section2">
        <div class="slide slide01">
          <div class="s2-1">
            <div class="s2-1title">质感进化</div>
            <div class="s2-1content">
              <div class="s2-1cL"></div>
              <div class="s2-1cR">
                <span>暗物质、骇客银、</span>
                <span>青空结界，</span>
                <span>质感再进化。</span>
              </div>
            </div>
          </div>
        </div>
        <div class="slide slide02">
          <div class="s2-2">
            <div class="s2-2L">
              <img v-if="WstyleS === 'black'" src="../assets/fullpage/images/black-0-b64a75.jpg.webp" alt="">
              <img v-else-if="WstyleS === 'silver'" src="../assets/fullpage/images/silver-0-d9799f.jpg.webp" alt="">
              <img v-else src="../assets/fullpage/images/green-0-eff039.jpg.webp" alt="">
            </div>
            <div class="s2-2R">
              <div class="s2-2R_text" v-if="WstyleS === 'black'">
                <span>不发光，却令一切黯然失色。</span>
                <span>沉稳深邃，黑得漂亮。</span>
                <span>第二代丝绸玻璃工艺，手感丝滑细腻，</span>
                <span>只留舒适，不留汗渍。</span>
              </div>
              <div class="s2-2R_text" v-else-if="WstyleS === 'silver'">
                <span>一亮相，就迎来它的高光时刻。</span>
                <span>漫反射渐变纹理再升级，惹人喜爱，却不惹指纹。</span>
                <span>硬核中带着温和的精致，光影交错间，</span>
                <span>速度感与金属质感满满。</span>
              </div>
              <div class="s2-2R_text" v-else>
                <span>一出场，便自带C位气场</span>
                <span>特有漫反射渐变纹理与蒸镀 OCO 工艺叠加，</span>
                <span>焕发出通透、柔和、丰富的艺术光泽，</span>
                <span>宛若碧海之上闪耀着的道道极光。</span>
              </div>
              <div class="s2-2R_Img">
                <img class="s2smallImg" v-if="WstyleS === 'black'" src="../assets/fullpage/images/black-1-156b79.png.webp" alt="">
                <img class="s2smallImg" v-else-if="WstyleS === 'silver'" src="../assets/fullpage/images/silver-1-3b435c.png.webp" alt="">
                <img class="s2smallImg" v-else src="../assets/fullpage/images/green-1-d9eb81.png.webp" alt="">
              </div>
            </div>
            <div class="s2-2Btn">
              <div @click="switch01" class="switch01"></div>
              <div @click="switch02"></div>
              <div @click="switch03"></div>
            </div>
          </div>
        </div>
      </div>
      <div class="section section3">
        <div class="section3-img">
          <img class="a1" src="../assets/fullpage/images//01.jpg.webp" alt="">
          <img class="a2" src="../assets/fullpage/images//02.jpg.webp" alt="">
          <img class="a3" src="../assets/fullpage/images//03.jpg.webp" alt="">
          <img class="a4" src="../assets/fullpage/images//04.jpg.webp" alt="">
        </div>
      </div>
      <div class="section">
      </div>
    </full-page>
  </div>
</template>

<script>
import '../assets/fullpage/css/jquery.fullPage.css'
import $ from 'jquery'
export default {
  data () {
    return {
      options: {
        licenseKey: 'OPEN-SOURCE-GPLV3-LICENSE',
        afterLoad: this.afterLoad,
        onLeave: this.onLeave,
        scrollOverflow: true,
        showActiveTooltip: true,
        controlArrows: true,
        scrollBar: false,
        paddingTop: '80px'
      },
      WstyleS: 'black'
    }
  },
  created () {
  },
  methods: {
    // 回调函数
    // 屏幕滚到某一屏后的回调函数
    afterLoad (origin, destination, direction) {
      if (destination.index === 1) {
        $('.s1_left').animate({ width: 600, height: 400, left: 190 }, 800, function () {
          $('.s1_text').animate({ opacity: 1 }, 1000)
        })
        $('.s1_right').animate({ width: 600, height: 400, right: 190 }, 800)
      }
      if (destination.index === 2) {
        $('.s2-1cL').animate({ width: '40%' }, 1000)
        $('.s2-1cR').animate({ height: 250 }, 1000)
      }
      if (destination.index === 3) {
        $('.a1').animate({ height: '100%' }, 100, function () {
          $('.a2').animate({ height: '80%' }, 100, function () {
            $('.a3').animate({ height: '60%' }, 100, function () {
              $('.a4').animate({ height: '40%' }, 100)
            })
          })
        })
      }
    },
    // 刚开始滚动屏幕就触发的回调函数
    // 滚动前的回调函数
    // 判断往上滚动还是往下滚动，值是up 或 down
    onLeave (origin, destination, direction) {},
    switch01 () {
      this.WstyleS = 'black'
      $('.s2smallImg').css({ transform: 'translateY(0)' })
    },
    switch02 () {
      this.WstyleS = 'silver'
      $('.s2smallImg').css({ transform: 'translateY(-260px)' })
    },
    switch03 () {
      this.WstyleS = 'cyan'
      $('.s2smallImg').css({ transform: 'translateY(-520px)' })
    }
  }
}
</script>

<style src='../assets/fullpage/css/index.css' scoped></style>
<style scoped>
#fullpage{
    z-index:99;
}
</style>
