<template>
  <div class="smallAssembly">
    <button class="btn" :disabled='cut' @click="cutNum">-</button>
    <input class="number" type="text" ref="number" :value="indexNum" @keyup.13="keyup"
    oninput="value=value.replace(/[^\d]/g,'')">
    <button class="btn" :disabled='add' @click="addNum" >+</button>
  </div>
</template>

<script>
export default {
  // 最大值，初始值，商品id，样式
  props: ['max', 'initCount', 'PDid'],
  data() {
    return {
      id: this.PDid,
      maxNum: this.max,
      indexNum: this.initCount,
      cut: false,
      add: false
    }
  },
  watch: {
    'indexNum'() {
      this.countC()
    },
    'max' (val) {
      this.maxNum = this.max
    },
    'PDid' (val) {
      this.id = this.PDid
    }
  },
  methods: {
    // 减
    cutNum () {
      if (this.indexNum > 1) {
        this.indexNum--
        this.add = false
      } else {
        this.cut = true
      }
    },
    // 加
    addNum () {
      if (this.indexNum < this.maxNum) {
        this.indexNum++
        this.cut = false
      } else {
        this.add = true
        alert('当前选购数量大于库存量')
      }
    },
    countC () {
      // 将数值输入框中的数量返回 ProductDetails 页面
      const count = parseInt(this.indexNum)
      this.$emit('PDcount', { count: count, id: this.id })
    },
    keyup () {
      if (this.$refs.number.value > this.maxNum) {
        this.indexNum = ''
        this.indexNum = '10'
        alert('当前选购数量大于库存量，已自动帮您选取最大货存量')
      } else {
        this.indexNum = this.$refs.number.value
      }
    }
  }
}
</script>

<style scoped>
.smallAssembly{
  width: 150px;
  height: 50px;
  overflow: hidden;
  background-color: rgb(0, 247, 255);
  display: flex;
}
.smallAssembly .btn{
  width: 50px;
  height: 50px;
  font-size: 40px;
  border-radius: 5px;
  text-align: center;
}
.smallAssembly .number{
  width: 50px;
  height: 50px;
  text-align: center;
  line-height: 50px;
}
</style>
