export default {
  state: {
    id: 0,
    username: ''
  },
  //  数据处理
  mutations: {
    setUser (state, user) {
      state.id = user.id
      state.username = user.username
    },
    // 清空操作
    logout (state) {
      state.id = 0
      state.username = ''
    }
  },
  actions: {},
  // 计算属性
  getters: {
    isLogin (state) {
      return state.id !== 0
    }
  },
  namespaced: true
}
