<template>
  <div id="app">
    <router-view name="notPage" :class="{ none: isNone }"></router-view>
    <!-- 头部组件 -->
    <top :class="{ none: isNone }"></top>
    <!-- 挂广告 -->
    <div ref="jzai" id="advertisement" >
      <div class="ad_body">
        <section class="container">
          <div class="tittle">
            <h2>Loading</h2>
          </div>
          <div class="square-container">
            <div class="square square1">&nbsp;</div>
            <div class="square square2">&nbsp;</div>
            <div class="square square3">&nbsp;</div>
            <div class="square square4">&nbsp;</div>
            <div class="square square5">&nbsp;</div>
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
// 导入
import top from './components/01.top.vue'

export default {
  data () {
    return {
      isNone: true
    }
  },
  created () {
    this.checkLogin()
  },
  mounted: function () {
    this.$nextTick(function () {
      var thist = this
      setTimeout(function () {
        thist.isNone = false
        thist.$refs.jzai.remove()
      }, 3000)
    })
  },
  methods: {
    checkLogin () {
      this.$http.get('user').then(res => {
        if (res.data.code === 1) {
          // 如果用户已经登陆了,那么就将用户信息保存到vueX中
          this.$store.commit('user/setUser', res.data.data)
        }
      })
    }
  },
  // 注册
  components: {
    top
  }
}
</script>

<style src='./assets/css/App.css' scoped></style>
<style>
* {
  margin: 0;
  padding: 0;
  list-style: none;
  text-decoration: none;
}
#app .player:hover{
  transform: translateY(0)!important;
}
</style>
