<template>
    <div class="address">
      <!--======== 收货人信息 =========-->
      <div class="consignee">
        <div class="AC_left">收货人信息</div>
        <div class="AC_right" @click="openModal01">新增加收货地址</div>
      </div>
      <!--======== 内容 =========-->
      <div class="add_main">
        <div class="address_item">
          <!-- 名字 -->
          <div class="AM_name">{{defaultList.name}}</div>
          <!-- 地址信息 -->
          <div class="AM_add">
            <div class="area">{{defaultList.area}}</div>
            <div class="detail">{{defaultList.detail}}</div>
            <div class="tel">{{defaultList.tel}}</div>
          </div>
          <!-- 操作 -->
          <div class="AM_operation">
          </div>
        </div>
      </div>
      <!--======== 更多地址 =========-->
      <div class="more"><span @click="openModal02">更多地址</span></div>
      <!-- 模态框（新增收获地址） -->
      <div class="increase">
        <div class="modalTop">
          <div class="MT_text"><span>新增收货人信息</span></div>
          <div class="MT_iocn iconfont icon-guanbi" @click="closeModal01"></div>
        </div>
        <dir class="modalBody">
          <form action="#" class="modelForm">
            <div class="formName"><span>收货人</span><input type="text" v-model="from.name" name="name" min="2" max="5" placeholder="请输入收货人名称" class="input"></div>
            <div class="formNumber"><span>手机号码</span><input type="text" v-model="from.tel" name="tel" placeholder="请输入手机号码" class="input"></div>
            <div class="formArea">
              <span>请输入所在地</span><input type="text" v-model="from.area" name="area" placeholder="请输入所在地" class="input">
            </div>
            <div class="formDetail"><span>详细地址</span><input type="text" v-model="from.detail"  name="detail" placeholder="请输入手详细地址" class="input"></div>
            <div class="formBtn" @click="foundInformation()">保存收货人信息</div>
          </form>
        </dir>
      </div>
      <!-- 模态框（更多地址） -->
      <div class="modalAddress" :class="{modalA : isModalA}">
        <div class="address_item" v-for="(item, index) in AddressList" :key="index">
          <!-- 名字 -->
          <div class="AM_name">{{item.name}}</div>
          <!-- 地址信息 -->
          <div class="AM_add">
            <div class="area">{{item.area}}</div>
            <div class="detail">{{item.detail}}</div>
            <div class="tel">{{item.tel}}</div>
          </div>
          <!-- 操作 -->
          <div class="AM_operation">
            <span @click="deleteAddress(item.id)">删除</span>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
import $ from 'jquery'
export default {
  data() {
    return {
      from: {
        id: '',
        name: '',
        // 手机号
        tel: '',
        // 收货地址
        area: '',
        // 详细地址
        detail: ''
      },
      newID: '',
      // 样式切换
      isModalA: true,
      falg: true,
      // 收货地址列表
      AddressList: [],
      // 默认地址列表
      defaultList: {}
    }
  },
  created () {
    this.defaultAddress()
  },
  watch: {
    defaultList () {
      this.ReturnID(this.defaultList.id)
    }
  },
  methods: {
    // 打开模态框
    openModal01 () {
      $('.increase').show()
      this.falg = !this.falg
    },
    // 关闭模态框
    closeModal01 () {
      $('.increase').hide()
      this.falg = !this.falg
    },
    openModal02 () {
      if (this.falg === true) {
        this.isModalA = !this.isModalA
        this.getAddressList()
      }
    },
    // 新建收货信息
    foundInformation () {
      this.$http
        .post('address/save', this.from)
        .then(res => {
          // console.log(res)
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            alert(res.data.msg)
            this.closeModal01()
            this.defaultAddress()
          }
        })
    },
    // 查询收货地址列表
    getAddressList () {
      this.$http
        .get('address')
        .then(res => {
          if (res.data.code === 2) {
            alert(res.data.msg)
            this.$router.push('/denglu')
          } else if (res.data.code === 0) {
            alert(res.data.msg)
          } else {
            this.AddressList = res.data.data
          }
        })
    },
    // 删除收货地址
    deleteAddress (ID) {
      this.$http.post('address/del', { id: ID }).then(res => {
        this.getAddressList()
      })
    },
    defaultAddress () {
      this.$http.get('address/def', { id: '' }).then(res => {
        if (res.data.code === 2) {
          alert(res.data.msg)
          this.$router.push('/denglu')
        } else if (res.data.code === 0) {
          alert(res.data.msg)
        } else {
          this.defaultList = res.data.data
        }
      })
    },
    ReturnID (ID) {
      this.$emit('ReturnID', ID)
    }
  }
}
</script>

<style scoped>
.address{
  position: relative;
  width: 1080px;
  min-height: 165px;
  border-bottom: 2px solid rgba(0, 0, 0, .3);
    -moz-user-select: -moz-none;
    -moz-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.address .consignee{
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.address .consignee .AC_left{
  margin-left: 10px;
  font-weight: bold;
}
.address .consignee .AC_right{
  margin-right: 10px;
  font-size: 14px;
  color: rgb(108, 184, 255);
  cursor: pointer;
}
/* 内容 */
.address  .add_main{
  height: 90px;
  overflow: hidden;
}
.address .address_item{
  width: 980px;
  height: 80px;
  margin: 0 auto;
  margin-bottom: 10px;
  background-color: rgb(63, 54, 54);
  color: #fff;
  display: grid;
  grid-template-columns: 15% 70% 15%;
  text-align: center;
  border-radius: 10px;
}
/* 名字 */
.address .address_item .AM_name{
  font-weight: bold;
  line-height: 80px;
  color: rgb(255, 255, 255);
}
/* 地址信息 */
.address .address_item .AM_add{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  /* background-color: rgb(86, 249, 255); */
}
/* 操作按钮 */
.address .address_item .AM_operation{
  font-size: 14px;
  line-height: 80px;
  color: rgb(124, 196, 255);
  cursor: pointer;
}
.address .address_item .AM_operation>span{
  margin-right: 15px;
}
/* 更多按钮 */
.address .more{
  margin-left: 50px;
  font-size: 14px;
}
.address .more>span{
  cursor: pointer;
}
/* =====================模态框============== */
.address .increase{
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  width: 500px;
  height: 500px;
  background-color: rgb(233, 233, 233);
  border: 5px solid rgb(83, 79, 79);
  z-index: 5;
  display: none;
}
/* 头部 */
.address .increase .modalTop{
  display: flex;
  justify-content: space-between;
  width: 480px;
  height: 30px;
  line-height: 30px;
  padding: 0 10px 0 10px;
  margin-bottom: 20px;
  background-color: rgb(199, 199, 199);
}
.address .increase .modalTop .MT_iocn{
  cursor: pointer;
}
/* 表单 */
.address .increase .modelForm{
  width: 460px;
  height: 350px;
  margin: 0 auto;
  padding-top: 2px;
}
.address .increase .modelForm div{
  margin-top: 20px;
  width: 100%;
  height: 37px;
  overflow: hidden;
}
.address .increase .modelForm span{
  display: inline-block;
  width: 120px;
  height: 35px;
}
.address .increase .modelForm input{
  margin-left: 10px;
  width: 200px;
  height: 35px;
  padding-left: 10px;
  border-radius: 8px;
  border: 0;
  background-color: #fff;
}
.address .increase .modelForm .formBtn{
  margin-top: 50px;
  width: 150px;
  height: 30px;
  font-size: 14px;
  font-weight: bold;
  line-height: 30px;
  text-align: center;
  border-radius: 5px;
  border: .5px solid #000;
  background-color: rgb(233, 57, 57);
  color: #fff;
  cursor: pointer;
}
.address .modalAddress {
  position: absolute;
  top: 170px;
  left: 0;
  width: 1080px;
  min-height: 90px;
  padding-top: 10px;
  border: 5px solid rgba(20, 15, 15,.5);
  border-radius: 10px;
  background-color: rgb(155, 155, 155);
  z-index: 4;
  display: block;
}
.address .modalAddress.modalA{
  display: none!important;
}
</style>
