<template>
  <div id="ProductDetails">
    <div class="PD_Top">
      <!-- 轮播图 -->
      <div class="banner">
        <el-carousel trigger="click" height="500px" :interval='5000'>
          <el-carousel-item v-for="(item, index) in spBanner" :key="index">
          <div class="img">
            <img :src="item.img" alt="">
          </div>
          </el-carousel-item>
        </el-carousel>
      </div>
      <!-- 商品购买内容 -->
      <div class="content">
        <div class="PD_sell_point">
          <span>{{spDetails.sell_point}}</span>
        </div>
        <!-- 名字 -->
        <div class="PD_name">
          <span>{{spDetails.name}}</span>
        </div>
        <!-- 价格 -->
        <div class="PD_price">
          <span>￥ </span><span class="PD_num">{{spDetails.price}}</span>
        </div>
        <!-- 计算器 -->
        <div class="PD_calculator">
          <numbox @PDcount="countChanged" :max='spDetails.num' initCount="1" :PDid='spDetails.id'></numbox>
        </div>
        <!-- 按钮 -->
        <div class="PD_btn">
          <el-row>
            <el-button class="PD_btn_left" type="primary" round>立马下单</el-button>
            <el-button class="PD_btn_right" type="success" @click="addCar" round>加入购物车</el-button>
        </el-row>
        </div>
      </div>
    </div>
    <fullpage class="fullpage"></fullpage>
  </div>
</template>

<script>
import fullpage from './H.fullpage.vue'
import numbox from '../components/smallAssembly.vue'
export default {
  name: 'ProductDetails',
  props: ['id'],
  components: {
    numbox,
    fullpage
  },
  data () {
    return {
      // 商品详细列表
      spDetails: [],
      // 商品轮播图
      spBanner: [],
      // 购买的数量
      selectdeCount: 1
    }
  },
  created () {
    // 获取商品详细
    this.getProductDetails()
  },
  methods: {
    // 获取商品详细
    getProductDetails () {
      // console.log(this.id)
      this.$http
        .get('goodsinfo', {
          params: {
            id: this.id
          }
        })
        .then(res => {
          // console.log(res)
          if (res.data.code === 1) {
            if (res.data.data) {
              this.spDetails = res.data.data
              this.spBanner = res.data.data.album
            } else {
              alert('商品内容为空')
            }
          } else {
            alert(res.data.msg + '请求失败')
          }
        })
    },
    // 点击加减号的时候触发这个方法
    countChanged (val) {
      // console.log(val)
      this.selectdeCount = val.count
    },
    // 添加购物车
    addCar () {
      this.$store.commit('shopcart/addShopcart', {
        id: parseInt(this.id),
        count: this.selectdeCount,
        selected: true
      })
    }
  }
}
</script>

<style src='../assets/homecss/ProductDetails.css' scoped></style>
<style scoped>
.fullpage{
  z-index: 99;
}
</style>
