<template>
  <div id="ProductList">
    <!-- 内容 -->
    <div class="content">
      <div class="items" v-for="(item, index) in productList" :key="index">
        <!-- 图片 -->
        <router-link :to="'/productDetails/' + item.id">
          <div class="content_items_img">
            <img :src="item.image" alt="">
          </div>
        </router-link>
        <!-- 价格 -->
        <div class="content_items_price">
          <span class="one">￥</span><span class="two">{{item.price}}</span>
        </div>
        <!-- 名字 -->
        <div class="content_items_name">
          <span>{{item.name}}</span>
        </div>
        <!-- 卖点 -->
        <div class="content_items_introduce" :class="{ describe_text: isdescribe_text }">
          {{item.sell_point}}
        </div>
        <!-- 图标 -->
        <div class="content_items_icon iconfont icon-xiala" v-if="isdescribe_text === true" @click="modify"></div>
        <div class="content_items_icon iconfont icon--shanglajiantou" v-else @click="modify"></div>
        <!-- 库存 -->
        <div class="content_items_num">
          <span>库存 </span><span>{{item.num}}</span>
        </div>
      </div>
    </div>
    <!-- 返回顶部 -->
    <div class="backT">
      <back-top></back-top>
    </div>
    <statement></statement>
  </div>
</template>

<script>
import backTop from '../components/backTop.vue'
import statement from '../views/A.statement.vue'
export default {
  name: 'ProductList',
  props: ['category_id'],
  components: {
    backTop,
    statement
  },
  data() {
    return {
      // 样式修改
      isdescribe_text: true,
      // 商品列表
      productList: []
    }
  },
  created() {
    this.getGoodsLost()
  },
  methods: {
    // 内容
    getGoodsLost() {
      // console.log(this.category_id)
      this.$http
        .get('goodslist', {
          params: {
            category_id: this.category_id,
            last_id: 0
          }
        })
        .then(res => {
          // console.log(res)
          if (res.data.code === 1) {
            this.productList = res.data.data
          }
        })
    },
    // 点击改变样式
    modify () {
      this.isdescribe_text = !this.isdescribe_text
    }
  }
}
</script>

<style src='../assets/homecss/ProductList.css' scoped></style>
<style scoped>
#ProductList{
  padding-top: 80px;
}
#ProductList .backT{
  position: fixed;
  top: 250px;
  right: 50px;
}
#ProductList .content{
  min-height: 100vh;
}
#ProductList .zhanwei01{
    width: 100%;
    height: 80px;
    background-color: #242424;
}
#ProductList >>> .el-button.is-round{
    padding: 9px 21px;
}
</style>
