<template>
  <div id="top" >
    <div class="top_T">
      <div class="top_T_nav">
        <!-- logo -->
        <div class="top_T_logo">
          <img src="../assets/images/logo.jpeg" alt="" />
        </div>
        <!-- tab -->
        <ul class="top_T_ul">
          <router-link to="/home">
            <li>首页</li> </router-link
          ><router-link to="/shopping"> <li>购物车</li></router-link
          ><router-link to="/my"> <li>我的</li></router-link
          ><router-link to="/Video"> <li>视频</li></router-link
          ><router-link to="/myMusic/personality"> <li>我的音乐</li></router-link
          >
        </ul>
        <!-- 搜索 -->
        <div class="top_T_inp">
          <input type="text" v-model="inputValue" @keyup.enter="toResult" />
          <!-- 双向绑定一个数据  监听键盘回车键 -->
        </div>
        <!-- 登录 -->
        <div class="top_T_dl01" v-if="$store.getters['user/isLogin']">
          <a href="javaScript:;">{{$store.state.user.username}}</a>
          <div class="signOut" @click="signOut">退出登录</div>
        </div>
        <div class="top_T_dl02" v-else>
          <router-link to="/denglu">登录</router-link>
        </div>
      </div>
    </div>
    <div class="top_B">
      <!-- 路由的出口 地址命中之后，把组件显示的位置 -->
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 输入的内容
      inputValue: []
    }
  },
  methods: {
    toResult () {
      // 如果为空 提示
      if (this.inputValue === '') {
        alert('请输入内容')
      } else {
        // 否则跳转
        // this.$router.push('/result')
        // 携带数据
        this.$router.push('/result?cx= ' + this.inputValue)
      }
    },
    signOut () {
      this.$http.get('logout')
      this.$auth.setAuthorization('')
      // 删除localStorage里面的会话id
      this.$store.commit('user/logout')
      // 将Vuex里面的用户信息删除
    }
  }
}
</script>

<style scoped>
#top {
  position: relative;
  width: 100%;
  z-index: 999;
}
#top .top_T{
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background-color: #242424;
  opacity: .8;
  z-index: 9999;
}
#top .top_T .top_T_nav{
  width: 1100px;
  height: 80px;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 10% 60% 20% 10%;
  overflow: hidden;
}
/* logo */
#top .top_T .top_T_logo{
  width: 70px;
  height: 70px;
  margin-top: 6px;
  border-radius: 10px;
  overflow: hidden;
}
#top .top_T .top_T_logo img{
  width: 70px;
  height: 70px;
}
/* tab */
#top .top_T .top_T_ul{
  display: flex;
}
#top .top_T .top_T_ul a{
  width: 20%;
  height: 80px;
  line-height: 80px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  color: rgb(216, 216, 216);
}

/* 路由点击高亮 */
#top .top_T_ul a.router-link-active {
  color: rgb(189, 16, 16);
}

/* 搜索栏 */
#top .top_T_inp {
  line-height: 80px;
}
#top .top_T_inp input {
  background-color: #fff;
  padding: 0.5rem 0.5rem;
  margin: 0.5rem auto;
  border-radius: 30px;
  border: none;
  outline: medium;
}
/* login */
#top .top_T .top_T_dl01,.top_T_dl02{
  line-height: 80px;
  text-align: center;
}
#top .top_T .signOut{
  position: absolute;
  top: 50px;
  font-weight: bold;
  font-size: 20px;
  color: #6b6b6b;
  display: none;
  cursor: pointer;
}
#top .top_T .top_T_dl01:hover .signOut{
  display: block;
}
#top .top_T .top_T_dl02>a{
  font-size: 20px;
  color: #6b6b6b;
}
#top .top_T .top_T_dl01>a{
  font-size: 20px;
  color: #6b6b6b;
}
#top .top_B{
  min-width: 1366px;
}
</style>
